}

.icon-basket {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-close {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-facebookn {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-google-plus {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-grid {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-heart {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-in {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-list {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-question {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-search {
  width: 1em;
  height: 1em;
