}

body {
  min-width: 320px;
  color: #828793;
  font-family: "Open Sans";
  font-size: 14px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.inner {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 20px;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  position: relative;
}

@media only screen and (max-width: 1260px) {
  .header__inner {
    flex-direction: column;
    align-items: center;
  }
}

.header__bottom-inner {
  position: relative;
}

.header__top {
  border-bottom: 1px solid #e8e8e8;
  padding: 30px 0 15px;
}

@media only screen and (max-width: 767px) {
  .header__top {
    padding: 15px 0;
  }
}

.header__right {
  display: flex;
  align-items: center;
}

.header__categories {
  margin-left: 35px;
}

@media only screen and (max-width: 767px) {
  .header__categories {
    margin-left: 10px;
  }
}

.header__user {
  float: right;
  display: flex;
  align-items: center;
  padding: 20px 0;
}

@media only screen and (max-width: 1260px) {
  .header__user {
    float: none;
  }
}

@media only screen and (max-width: 767px) {
  .header__user {
    display: block;
    text-align: center;
  }
}

.header__search {
  width: 330px;
  margin-left: 25px;
}

@media only screen and (max-width: 767px) {
  .header__search {
    width: 30px;
    margin-left: 10px;
  }
}

.header__burger {
  position: absolute;
  right: 20px;
  top: -55px;
}

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.logo__img {
  max-width: 73px;
  flex: none;
}

@media only screen and (max-width: 767px) {
  .logo__img {
    max-width: 50px;
  }
}

.logo__img img {
  display: block;
  max-width: 100%;
}

.logo__title, .logo__title-dark {
  padding: 15px 0 20px;
  font-size: 30px;
  font-weight: 600;
}

@media only screen and (max-width: 767px) {
  .logo__title, .logo__title-dark {
    font-size: 20px;
    padding: 20px 0;
  }
}

.logo__title {
  color: #828793;
}

.logo__title-dark {
  color: #23d9a5;
}

.categories {
  font-size: 16px;
  text-transform: uppercase;
}

@media only screen and (max-width: 767px) {
  .categories {
    font-size: 12px;
  }
}

.categories__icon, .categories__title {
  display: inline-block;
  vertical-align: middle;
}

.categories__icon .icon-question {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
  fill: #828793;
}

.search {
  position: relative;
}

.search__input {
  width: 100%;
  border: 1px solid #e8e8e8;
  padding: 12px;
  font-size: 16px;
}

@media only screen and (max-width: 767px) {
  .search__input {
    display: none;
  }
}

.search__icon {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
}

.search__icon .icon-search {
  font-size: 18px;
  display: inline-block;
  vertical-align: middle;
  fill: #828793;
}

.user__icon-block {
  position: relative;
  display: inline-block;
  text-decoration: none;
}

.user__icon-block:not(:first-child) {
  margin-left: 35px;
}

.user__icon .icon-basket, .user__icon .icon-heart {
  transition: fill 0.5s;
  fill: #828793;
}

.user__icon .icon-basket:hover, .user__icon .icon-heart:hover {
  fill: #23d9a5;
}

.user__notification {
  position: absolute;
  line-height: 20px;
  text-align: center;
  top: -20px;
  right: -20px;
  font-size: 10px;
  background-color: #def8f2;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  color: #23d9a5;
}

.user__notification-bottom {
  position: absolute;
  color: #23d9a5;
  right: -13px;
  top: 0;
  font-size: 10px;
}

.user__button-exit {
  padding-left: 40px;
}

@media only screen and (max-width: 767px) {
  .user__button-exit {
    padding-left: 0;
    margin-bottom: 20px;
    margin-top: 20px;
  }
}

.user__login {
  padding-left: 25px;
  font-size: 16px;
  text-transform: uppercase;
}

@media only screen and (max-width: 767px) {
  .user__login {
    padding-left: 0;
    margin-bottom: 20px;
  }
}

.user__login-in {
  position: relative;
  padding-right: 9px;
  text-decoration: none;
  color: #828793;
  transition: color 0.5s;
}

.user__login-in:before {
  content: '';
  position: absolute;
  right: -1px;
  height: 10px;
  width: 1px;
  background-color: #fafafa;
}

.user__login-in:hover {
  color: #23d9a5;
}

.user__login-up {
  color: #828793;
  transition: color 0.5s;
  padding-left: 9px;
  text-decoration: none;
}

.user__login-up:hover {
  color: #23d9a5;
}

.button-exit {
  color: #def8f2;
  text-transform: uppercase;
  padding: 10px 12px;
  background-color: #23d9a5;
  border-radius: 4px;
  position: relative;
  transition: background-color 0.5s;
}

.button-exit:hover {
  background-color: #1cad84;
}

.menu {
  font-size: 0;
}

@media only screen and (max-width: 1023px) {
  .menu {
    background: gray;
    z-index: 10;
    top: -55px;
    position: absolute;
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%);
    transition: opacity 0.5s, transform 0.5s;
    left: -20px;
    padding: 20px;
  }
  .menu.is-open {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
}

.menu__item {
  display: inline-block;
  padding: 35px 40px 35px 0;
}

@media only screen and (max-width: 1023px) {
  .menu__item {
    display: block;
    padding: 10px;
  }
}

.menu__link {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
  transition: color 0.5s;
  color: #828793;
}

.menu__link.is-active, .menu__link:hover {
  color: #23d9a5;
}

@media only screen and (max-width: 1023px) {
  .menu__link {
    color: #ffffff;
  }
}

.burger {
  width: 20px;
  height: 20px;
  display: none;
}

@media only screen and (max-width: 1023px) {
  .burger {
    display: block;
  }
}

.burger__line-top {
  position: absolute;
  width: 20px;
  height: 1px;
  background-color: gray;
  transform: translateY(0);
  transform-origin: top left;
  transition: transform 0.5s;
}

.is-open .burger__line-top {
  transform: translateY(-5px) rotate(45deg);
}

.burger__line-center {
  position: absolute;
  width: 20px;
  height: 1px;
  background-color: gray;
  transform: translateY(5px);
}

.is-open .burger__line-center {
  opacity: 0;
}

.burger__line-bottom {
  position: absolute;
  width: 20px;
  height: 1px;
  background-color: gray;
  transform: translateY(10px);
  transform-origin: top left;
  transition: transform 0.5s;
}

.is-open .burger__line-bottom {
  transform: translateY(9px) rotate(-45deg);
}

.filter__top-line {
  display: flex;
  justify-content: space-between;
}

.filter__title {
  margin-bottom: 30px;
  text-transform: uppercase;
}

.filter__list-buttons {
  margin-bottom: 40px;
}

.filter__tabs {
  margin-bottom: 40px;
}

.list-buttons {
  font-size: 0;
}

@media only screen and (max-width: 767px) {
  .list-buttons {
    text-align: center;
  }
}

.item-button {
  display: inline-block;
  vertical-align: middle;
}

@media only screen and (max-width: 767px) {
  .item-button {
    display: block;
  }
}

.item-button:not(:last-child) {
  margin-right: 65px;
}

@media only screen and (max-width: 767px) {
  .item-button:not(:last-child) {
    margin-right: 0;
    margin-bottom: 10px;
  }
}

.item-button__close {
  display: inline-block;
  vertical-align: middle;
  border-radius: 50%;
  height: 20px;
  font-size: 18px;
  margin-right: 10px;
}

.item-button__close .icon-close {
  fill: #828793;
}

.item-button__text {
  display: inline-block;
  vertical-align: middle;
  font-size: 14px;
}

.item-button__text-border {
  font-size: 14px;
  display: inline-block;
}

.item-button__text-border {
  padding: 13px 25px;
  border-radius: 20px;
  border: 1px solid #23d9a5;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.row:not(:last-child) {
  margin-bottom: 20px;
}

.col {
  padding: 0 15px;
  width: 25%;
}

@media only screen and (max-width: 1023px) {
  .col {
    width: 50%;
  }
}

@media only screen and (max-width: 767px) {
  .col {
    width: 100%;
  }
}

.tabs-line {
  display: flex;
  justify-content: space-between;
  background-color: #e8e8e8;
  align-items: center;
  padding: 10px 15px;
}

@media only screen and (max-width: 767px) {
  .tabs-line {
    display: block;
    text-align: center;
  }
}

.tabs-line__list {
  font-size: 0;
}

@media only screen and (max-width: 767px) {
  .tabs-line__list {
    margin-bottom: 20px;
  }
}

.tabs-line__item {
  display: inline-block;
  margin-right: 15px;
}

.tabs-line__item:last-child .tabs-line__link {
  border-right: none;
}

.tabs-line__link {
  text-decoration: none;
  color: #828793;
  font-size: 14px;
  display: inline-block;
  position: relative;
  transition: color 0.5s;
  padding-right: 15px;
  border-right: 1px solid #828793;
}

.tabs-line__link.is-active, .tabs-line__link:hover {
  color: #23d9a5;
}

.tabs-line__sort {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 767px) {
  .tabs-line__sort {
    justify-content: center;
  }
}

.checkbox__label {
  position: relative;
  padding-left: 30px;
  display: inline-block;
  cursor: pointer;
}

.checkbox__label:before {
  content: '';
  position: absolute;
  left: 0;
  width: 20px;
  height: 20px;
  border-radius: 3px;
  border: 1px solid #cacaca;
  background-color: #fff;
}

.checkbox__field {
  position: absolute;
  opacity: 0;
}

.checkbox__field:checked + .checkbox__label:before {
  background-color: #23d9a5;
}

.select {
  margin-bottom: 20px;
  position: relative;
}

.select__line {
  border: 1px solid #e8e8e8;
  border-radius: 5px;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}

.select__title {
  margin-bottom: 35px;
}

.select__block {
  opacity: 0;
  visibility: hidden;
  background-color: #ffffff;
  transition: opacity 0.2s, visibility 0.2s;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.5);
  border: 1px solid #e8e8e8;
  width: 100%;
  padding: 40px 20px 20px 20px;
  position: absolute;
  z-index: 10;
  top: 60px;
  left: 0;
}

.select__block:before {
  content: '';
  position: absolute;
  top: -15px;
  right: 0;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 12.5px 15px 12.5px;
  border-color: transparent transparent #fff transparent;
}

.is-open .select__block {
  opacity: 1;
  visibility: visible;
}

.select__item {
  margin-bottom: 13px;
  display: flex;
  justify-content: space-between;
}

.select__arrow .icon-arrow {
  fill: #828793;
}

.dropdown-sort {
  position: relative;
}

.dropdown-sort__btn {
  padding: 5px 10px;
  border: 1px solid #828793;
  border-radius: 5px;
  background-color: #ffffff;
  cursor: pointer;
  min-width: 80px;
}

.dropdown-sort__list {
  position: absolute;
  border-radius: 5px;
  top: 40px;
  padding: 10px;
  opacity: 0;
  visibility: hidden;
  z-index: 10;
  background-color: #ffffff;
  width: 100%;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.5);
  transition: opacity 0.5s, visibility 0.5s;
}

.is-open .dropdown-sort__list {
  opacity: 1;
  visibility: visible;
}

.dropdown-sort__item {
  margin-bottom: 5px;
}

.dropdown-sort__icon .icon-arrow {
  fill: #828793;
}

.sort__dropdown {
  margin: 0 15px 0 10px;
}

.sort__icon:last-child {
  margin-left: 10px;
}

.sort__icon .icon-list {
  font-size: 20px;
  fill: #828793;
}

.sort__icon .icon-grid {
  font-size: 20px;
  fill: #23d9a5;
}

.courses__gallery {
  margin: 0 -15px;
  display: flex;
  flex-wrap: wrap;
  padding-top: 30px;
  padding-bottom: 98px;
}

@media only screen and (max-width: 767px) {
  .courses__gallery {
    padding-bottom: 30px;
  }
}

.courses__item {
  padding: 0 15px;
  width: 25%;
  background-color: #ffffff;
  padding-bottom: 30px;
}

@media only screen and (max-width: 1023px) {
  .courses__item {
    width: 50%;
  }
}

@media only screen and (max-width: 767px) {
  .courses__item {
    width: 100%;
  }
}

.courses__wrap {
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.5);
}

.courses__title {
  font-size: 22px;
  color: #000000;
  text-decoration: none;
  transition: color 0.5s;
}

.courses__title:hover {
  color: #333333;
}

.courses__top {
  position: relative;
}

.courses__line-top {
  position: absolute;
  top: 0;
  right: 0;
  width: 100px;
  padding: 10px 15px;
  background-color: #23d9a5;
  transform-origin: right top;
  transform: rotate(90deg) translateX(100%);
  color: #ffffff;
  text-transform: uppercase;
  font-size: 10px;
}

.courses__line-bottom {
  position: absolute;
  bottom: -10px;
  left: 25px;
  padding: 5px 15px;
  background-color: #fe9142;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 10px;
}

.courses__img {
  padding-bottom: 180px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.courses__img:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
}

.courses__description {
  padding: 30px 20px;
}

.courses__teacher {
