}

.info__icon {
  display: inline-block;
  vertical-align: middle;
}

.info__icon .icon-user {
  font-size: 12px;
  fill: #828793;
}

.info__icon .icon-heart {
  font-size: 12px;
  fill: #828793;
}

.info__number {
  display: inline-block;
  vertical-align: middle;
  font-size: 10px;
}

.footer {
  background-color: #343a48;
}

.footer__top {
  display: flex;
  padding: 50px 0;
}

@media only screen and (max-width: 1260px) {
  .footer__top {
    display: block;
  }
}

@media only screen and (max-width: 767px) {
  .footer__top {
    padding: 30px 0;
  }
}

.footer__left {
  width: 40%;
}

@media only screen and (max-width: 1260px) {
  .footer__left {
    width: 100%;
    margin-bottom: 20px;
  }
}

.footer__right {
  width: 60%;
}

@media only screen and (max-width: 1260px) {
  .footer__right {
    width: 100%;
  }
}

.footer__row {
  display: flex;
  margin: 0 -10px;
}

@media only screen and (max-width: 767px) {
  .footer__row {
    width: 100%;
    display: block;
  }
}

.footer__col {
  width: 33.33%;
  padding: 0 10px;
}

@media only screen and (max-width: 767px) {
  .footer__col {
    width: 100%;
  }
  .footer__col:not(:last-child) {
    margin-bottom: 20px;
  }
}

.footer__info {
  padding: 20px 0 30px;
  font-size: 20px;
}

@media only screen and (max-width: 767px) {
  .footer__info {
    font-size: 14px;
  }
}

.footer__icons {
  display: flex;
}

.footer__icon {
  width: 40px;
  height: 40px;
  background-color: #424855;
  border-radius: 50%;
  text-align: center;
  line-height: 40px;
}

.footer__icon:not(:last-child) {
  margin-right: 15px;
}

@media only screen and (max-width: 767px) {
  .footer__icon:not(:last-child) {
    margin-right: 5px;
  }
