.footer__icon .icon-in {
  fill: #73767f;
  transition: fill 0.3s, transform 0.5s;
}

.footer__icon .icon-in:hover {
  fill: #23d9a5;
  transform: rotate(360deg);
}

.footer__icon .icon-facebook {
  fill: #73767f;
  transition: fill 0.3s, transform 0.5s;
